.github-stats-wrapper{
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}

.github-flexbox-light {
  display: none;
  width: 90%;
  margin: auto;
  justify-content: space-between;
  align-items: center;
}
.github-flexbox-light > div {
  /* width: 27%; */
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.github-flexbox-dark {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.progress-title {
  /* margin-bottom: 50px; */
  text-align: center;
}
.progress-title h2 {
  font-size: 14px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 10px;
  color: var(--color-title);
  letter-spacing: 3px;
  color: var(--color-title);
  font-weight: 600;
}
.progress-title h3 {
  text-transform: capitalize;
  font-size: 2.5rem;
  color: white;
}
.contro-calen{
  font-size: 14px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 10px;
  color: var(--color-title);
  letter-spacing: 3px;
  color: var(--color-title);
  font-weight: 600;
}
@media only screen and (max-width: 768px) {
  .github-flexbox-dark {
    flex-direction: column;
    gap: 30px;
  }
  .light .github-flexbox-light {
    flex-direction: column;
    gap: 30px;
  }
}
@media only screen and (max-width: 425px) {
  .github-flexbox-dark {
    flex-direction: column;
    gap: 30px;
  }
  .light .github-flexbox-light {
    flex-direction: column;
    gap: 30px;
  }
}
.light .github-flexbox-dark {
  display: none;
}
.light .github-flexbox-light {
  display: flex;
}
.light h3 {
  color: var(--color-info-light);
}
